

* {
  box-sizing: border-box;
}

body {
  background-color: #304e83;
  color: #1a1a1a;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  margin: 0;
}

.brand {
  text-decoration: none;
}

.root {
  display: flex;
  flex-direction: column;
}

.nav-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.main-title {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: bold;
  color: #304e83;
  padding: 0 0.5rem;
}

header {
  background-color: #ffcf29;
  display: flex;
  padding: 1rem;
  justify-content: flex-end;
  min-width: 540px;

}

.nav-components {
  margin: auto 0.5rem;
}

.search {
  border: 2px solid #304e83;
  border-radius: 50px;
  color: black;
  font-family: inherit;
  font-weight: bold;
  padding: 0.2rem 0.5rem;
  margin: auto 0.5rem;
  margin-top: 5%;
}

.search:focus {
  margin: 0;
}

.searchButton {
  background-color: white;
  border: 2px solid #304e83;
  border-radius: 50px;
  color: #304e83;
  padding: 0.2rem 0.5rem;
  font-family: inherit;
  font-weight: bold;
  padding: 0.5rem, 1.5rem;
  margin: auto 0.5rem;
  margin-top: 5%;
}

.sort-movies {
  display: flex;
  justify-content: center;
  margin: 2rem 1rem 0 0;
}

.sort-button {
  width: 15vw;
  background-color: rgba(0, 0, 0, 0.1);
  color: white;
  border: 2px solid #ffcf29;
  padding-top: 0.2rem;
  padding-bottom: 0.4rem;
  border-radius: 2rem;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: lighter;
  font-size: 100%;
  min-width: 100px;

}
.sort-button:hover {
  background-color: rgba(255, 255, 255, 0.3);
  font-weight: bold;
}

.top-rated-button {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-right: 1px solid #ffcf29;
}

.popular-button {
  border-radius: 0;
  border-right: 1px solid #ffcf29;
  border-left: 1px solid #ffcf29;
}

.latest-button {
  border-radius: 0;
  border-left: 1px solid #ffcf29;
  border-right: 1px solid #ffcf29;
}

.upcoming-button {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-left: 1px solid #ffcf29;
}

.jumbo {
  top: 0;
  height: 500px;
  width: 100%;
  position: relative;
  background-position: center center;
}

.jumbo-para {
  color: white;
  font-family: monospace;
  font-size: 1.25rem;
  font-weight: lighter;
  width: 75%;
  padding: 0;
}

.jumbo-button {
  background-color: rgba(255, 255, 255, 0.7);
  border: 2px solid white;
  border-radius: 50px;
  color: #304e83;
  padding: 0.5rem 1rem;
  width: 150px;
  font-size: 1.25rem;
  font-weight: bold;
}

.container {
  width: 100%;
}

.movie-container {
  display: flex;
  flex-wrap: wrap;
  max-width: 1200px;
  min-width: 540px;
  padding: 0 1rem;
  margin:0 auto;
}

.movie-outline {
  overflow: hidden;
  position: relative;
  margin: 1rem auto;
  width: 33%;
  min-width: 300px;
}

.movie {
  background-color: #ffcf29;
  border-radius: 3px;
  box-shadow: 3px 3px 5px rgba(0,0,0,0.1);
  overflow: hidden;
  position: relative;
  width: 90%;
  margin: 0 auto;
}

.movie img {
  width: 100%;
  height: auto;
  max-width: 100%;
}

.movie-info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}

.movie-info h3 {
  margin: 0;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
  font-weight: normal;
  font-size: 1.5rem;
}

.movie-over {
  background-color: white;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  box-shadow: 3px 3px 5px rgba(0,0,0,0.1);
  color: #333333;
  padding: 1rem;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  max-height: 100%;
  transform: translateY(102%);
  transition: transform 0.7s ease-in-out;
}

.movie:hover .movie-over {
  transform: translateY(0%);
}

.tag {
  background-color: #e4e4e4;
  padding: 0.3rem;
  text-shadow: 1px 1px 2px rgba(0,0,0,0.5);
  border: 2px solid rgb(26, 26, 26, 1);
  border-radius: 5px;
  font-weight: bold;
}

.tag.green {
  color: green;
}

.tag.orange {
  color: orange;
}

.tag.red {
  color: red;
}

.tag.black {
  color: black;
  font-size: 0.9rem;
  font-weight: normal;
}

.button-scroll {
  width: 8%;
  background-color: rgba(0, 0, 0, 0.1);
  color: white;
  border: 1px solid white;
  padding: 0.3rem 0.6rem;
  padding-top: 0.1rem;
  border-radius: 5px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: lighter;
  min-width: 100px;
  margin: 0px 5px;
}
.button-scroll:hover {
  background-color: rgba(255, 255, 255, 0.3);
  font-weight: bold;
}


.footer {
  background-color: #ffcf29;
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  padding: 0;
  margin-top: 100px;
}

.footer h3 {
  margin: auto;
}
